<template>
    <div id="app">
        <img src="./assets/logo.png" />
        <router-view />
    </div>
</template>

<script>

import api from '@/plugins/api';

export default {
    name: "App",
    mixins: [api],
    data() {
        return {
            product_data: {},
            productDataReady: false,
        };
    },
    created()
    {
        // this.$gapi.isSignedIn()
        //     .then(result => {
        //         console.log(result ? 'Signed in' : 'Signed out')
        //     });
        // this.getData();

        EventBus.$on('api_data_is_ready', (val)=>{
            console.log('api_data_is_ready');
            console.log(val);
            this.productData = val.data;
            this.productDataReady = true;
            console.log(this.productData);
        });
    }
};
</script>

<style>
#app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
</style>



