<template>
    <div>
        <h2>Login</h2>
        <b-form v-on:submit.stop.prevent>
            <label for="text-password">Enter your password</label>
            <b-input type="password" v-model="userPw" id="text-password" aria-describedby="password-help-block"></b-input>
            <b-form-text id="password-help-block">
                Please enter your password
            </b-form-text>
            <b-button variant="success" v-on:click="onsubmit">Submit</b-button>
        </b-form>
    </div>
</template>

<script>
export default {
    name: "Login",
    data() {
        return {
            userPw: ""
        };
    },
    methods: {
        onsubmit() {
            console.log("run onsubmit");
            console.log(this.userPw);
            if(this.userPw=='1116')
            {
                this.$router.push("Main");
            }
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
